
import axios from 'axios';
import CryptoItem from './CryptoItem.vue';

export default {
  data: function () {
    return {
      coins: [],
      selectedCurrency: 'eur', // Default currency
      userInput: '',
    };
  },
  mounted() {
    setTimeout(() => {
      this.loadCoins();
    }, 1500);
  },
  methods: {
    loadCoins: function () {
      axios
        .get(this.getApiUrl())
        .then((response) => {
          this.coins = response.data;
        })
        .catch((error) => {
          console.log(error);
        });
    },
    changeCurrency: function (currency) {
      this.selectedCurrency = currency;
      this.coins = [];
      setTimeout(() => {
        this.loadCoins();
      }, 1500);
    },
    getApiUrl: function () {
      return `https://api.coingecko.com/api/v3/coins/markets?vs_currency=${this.selectedCurrency}&order=market_cap_desc&per_page=10&page=1&sparkline=false&locale=en&x_cg_demo_api_key=CG-Dja2R2rhxMMJ9bYxERe3D27n`;
    },
    logUserInput: function () {
      const filteredCoins = this.coins.filter((coin) => {
        coin.name.toLowerCase().includes(this.userInput.toLowerCase());
      });
      if (this.userInput || filteredCoins.length > 1) {
        return (this.coins = filteredCoins);
      } else {
        return this.loadCoins();
      }
    },
  },
  components: {
    CryptoItem,
  },
};
